body {
    background-color: #f5f5f5;
}
a {
    color: #fff;
    font-weight: bold;
    text-decoration: none;
}
a:visited {
    color: #fff;
}
#root {
    width: 838px;
    margin: 0 auto;
}
#root ul {
    list-style: none;
    padding: 0;
}
.mainmenu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.mainmenu > li {
    width: 160px;
    height: 60px;
    background-color: #000e35;
    color: #f5f5f5;
    text-align: center;
    line-height: 60px;
    border-radius: 3px;
}
.mainmenu > li:nth-child(2) {
    line-height: 30px
}
.mainmenu > li > a {
    display: inline-block;
    width: 160px;
}
#visual {
    width: 838px;
    height: 300px;
    margin-bottom: 10px;
}
#visual span {
    display: none;
}
#visual a {
    display: inline-block;
    width: 100%;
    height: 300px;
    background: url(../public/images/main.png);
    cursor: pointer;
}
.h_div > h2 {
    display: inline-block;
    width: 100%;
    height: 50px;
    margin: 0;
    background: url(../public/images/0_띠배너/최다득표자_띠배너.png);
